import React, { useEffect, useState } from 'react'
import { motion } from 'framer-motion'
import { ArrowRight, Home } from 'lucide-react'
import { UniversalLink as Link } from '../aid/database/UniversalLink'

const page404 = () => {
  const [mousePosition, setMousePosition] = useState({ x: 0, y: 0 })
  const [isLoaded, setIsLoaded] = useState(false)

  useEffect(() => {
    setIsLoaded(true)
    const handleMouseMove = (e: MouseEvent) => {
      setMousePosition({ x: e.clientX, y: e.clientY })
    }

    if (typeof window !== 'undefined') {
      window.addEventListener('mousemove', handleMouseMove)
      return () => window.removeEventListener('mousemove', handleMouseMove)
    }
  }, [])

  const containerVariants = {
    hidden: { opacity: 0 },
    visible: {
      opacity: 1,
      transition: {
        staggerChildren: 0.15,
        delayChildren: 0.2,
      },
    },
  }

  const itemVariants = {
    hidden: { opacity: 0, y: 30 },
    visible: {
      opacity: 1,
      y: 0,
      transition: { duration: 0.8, ease: [0.21, 0.47, 0.32, 0.98] },
    },
  }

  const floatVariants = {
    hidden: { opacity: 0, scale: 0.8 },
    visible: {
      opacity: 1,
      scale: 1,
      transition: { duration: 1, ease: [0.21, 0.47, 0.32, 0.98] as [number, number, number, number] },
    },
    float: {
      y: [0, -20, 0],
      transition: {
        duration: 6,
        repeat: Infinity,
        ease: 'easeInOut',
      },
    },
  }

  return (
    <div className="error-404-container">
      {/* ANIMATED BACKGROUND ELEMENTS */}
      <div className="error-background-blur" />
      <div className="error-top-light" />
      <div className="error-vignette" />

      {/* ANIMATED MOUSE-TRACKING SPOTLIGHT */}
      <motion.div
        className="error-spotlight"
        animate={{
          left: `${mousePosition.x}px`,
          top: `${mousePosition.y}px`,
        }}
        transition={{ type: 'spring', damping: 10, mass: 0.1 }}
      />

      {/* FLOATING ORBS - DECORATIVE */}
      <motion.div
        className="orb orb-1"
        variants={floatVariants}
        initial="hidden"
        animate={isLoaded ? ['visible', 'float'] : 'hidden'}
      />
      <motion.div
        className="orb orb-2"
        variants={floatVariants}
        initial="hidden"
        animate={isLoaded ? ['visible', 'float'] : 'hidden'}
        transition={{
          ...floatVariants.float,
          delay: 1,
        }}
      />

      {/* MAIN CONTENT */}
      <motion.div
        className="error-content"
        variants={containerVariants}
        initial="hidden"
        animate={isLoaded ? 'visible' : 'hidden'}
      >
        {/* 404 NUMBER - LARGE */}
        <motion.div className="error-number-wrapper" variants={itemVariants}>
          <div className="error-number">404</div>
          <div className="error-glow" />
        </motion.div>

        {/* ERROR TITLE */}
        <motion.h1 className="error-title" variants={itemVariants}>
          Page Not Found
        </motion.h1>

        {/* ERROR DESCRIPTION */}
        <motion.p className="error-description" variants={itemVariants}>
          Looks like you've ventured into uncharted territory. The page you're looking for has either been moved, deleted, or never existed.
        </motion.p>

        {/* ACTION BUTTONS */}
        <motion.div className="error-actions" variants={itemVariants}>
          <Link to="/" className="error-button primary-button">
            <Home size={20} />
            <span>Back to Home</span>
            <ArrowRight size={18} style={{ marginLeft: '8px' }} />
          </Link>
        
        </motion.div>

        {/* DECORATIVE TEXT */}
     
      </motion.div>

      {/* ANIMATED GRID BACKGROUND */}
      <div className="error-grid" />

      {/* SCAN LINES EFFECT */}
      <div className="error-scan-lines" />

      <style jsx>{`
        .error-404-container {
          position: relative;
          width: 100vw;
          min-height: 100vh;
          display: flex;
          align-items: center;
          justify-content: center;
          overflow: hidden;
          background: #040508;
          color: #f8fafc;
        }

        /* BACKGROUND EFFECTS */
        .error-background-blur {
          position: absolute;
          inset: 0;
          background: radial-gradient(
            ellipse 80% 80% at 50% 50%,
            rgba(30, 58, 138, 0.08) 0%,
            transparent 70%
          );
          z-index: 1;
          pointer-events: none;
          filter: blur(60px);
        }

        .error-top-light {
          position: absolute;
          top: 0;
          left: 50%;
          width: 100%;
          height: 50%;
          transform: translateX(-50%);
          background: linear-gradient(
            to bottom,
            rgba(30, 64, 175, 0.1) 0%,
            transparent 100%
          );
          z-index: 2;
          pointer-events: none;
        }

        .error-vignette {
          position: absolute;
          inset: 0;
          background: radial-gradient(
            circle at center,
            transparent 40%,
            rgba(4, 5, 8, 0.8) 100%
          );
          z-index: 3;
          pointer-events: none;
        }

        /* MOUSE-TRACKING SPOTLIGHT */
        .error-spotlight {
          position: fixed;
          width: 400px;
          height: 400px;
          background: radial-gradient(
            circle,
            rgba(59, 130, 246, 0.15) 0%,
            transparent 70%
          );
          border-radius: 50%;
          filter: blur(40px);
          z-index: 1;
          pointer-events: none;
          transform: translate(-50%, -50%);
        }

        /* FLOATING ORBS */
        .orb {
          position: absolute;
          border-radius: 50%;
          filter: blur(40px);
          z-index: 2;
          pointer-events: none;
        }

        .orb-1 {
          width: 300px;
          height: 300px;
          top: 20%;
          right: 10%;
          background: radial-gradient(
            circle,
            rgba(59, 130, 246, 0.2) 0%,
            transparent 70%
          );
        }

        .orb-2 {
          width: 250px;
          height: 250px;
          bottom: 20%;
          left: 5%;
          background: radial-gradient(
            circle,
            rgba(30, 58, 138, 0.15) 0%,
            transparent 70%
          );
        }

        /* MAIN CONTENT */
        .error-content {
          position: relative;
          z-index: 10;
          text-align: center;
          display: flex;
          flex-direction: column;
          align-items: center;
          gap: 24px;
          max-width: 700px;
          padding: 40px;
          backdrop-filter: blur(10px);
        }

        /* 404 NUMBER */
        .error-number-wrapper {
          position: relative;
          margin-bottom: 20px;
        }

        .error-number {
          font-size: clamp(5rem, 15vw, 12rem);
          font-weight: 900;
          font-family: 'Outfit', sans-serif;
          letter-spacing: -0.05em;
          background: linear-gradient(135deg, #3b82f6 0%, #60a5fa 50%, #1e40af 100%);
          -webkit-background-clip: text;
          -webkit-text-fill-color: transparent;
          background-clip: text;
          position: relative;
          z-index: 2;
          text-shadow: 0 0 50px rgba(59, 130, 246, 0.3);
        }

        .error-glow {
          position: absolute;
          top: 50%;
          left: 50%;
          width: 100%;
          height: 100%;
          background: radial-gradient(
            circle,
            rgba(59, 130, 246, 0.2) 0%,
            transparent 70%
          );
          border-radius: 50%;
          filter: blur(40px);
          transform: translate(-50%, -50%);
          z-index: 1;
        }

        /* TITLE */
        .error-title {
          font-size: clamp(1.8rem, 6vw, 3.5rem);
          font-weight: 700;
          font-family: 'Outfit', sans-serif;
          letter-spacing: -0.02em;
          color: #f8fafc;
          margin: 0;
          line-height: 1.2;
        }

        /* DESCRIPTION */
        .error-description {
          font-size: 1.1rem;
          color: #94a3b8;
          max-width: 500px;
          line-height: 1.6;
          margin: 0;
          font-weight: 400;
        }

        /* ACTION BUTTONS */
        .error-actions {
          display: flex;
          gap: 16px;
          flex-wrap: wrap;
          justify-content: center;
          margin-top: 32px;
        }

        .error-button {
          display: inline-flex;
          align-items: center;
          gap: 12px;
          padding: 14px 32px;
          border-radius: 8px;
          font-size: 1rem;
          font-weight: 600;
          text-decoration: none;
          transition: all 0.3s cubic-bezier(0.19, 1, 0.22, 1);
          cursor: pointer;
          border: 1px solid transparent;
          white-space: nowrap;
        }

        .error-button.primary-button {
          background: linear-gradient(135deg, #1e40af, #3b82f6);
          color: #fff;
          border: 1px solid rgba(59, 130, 246, 0.5);
          box-shadow: 0 8px 24px rgba(30, 64, 175, 0.3);
        }

        .error-button.primary-button:hover {
          transform: translateY(-3px);
          box-shadow: 0 12px 32px rgba(30, 64, 175, 0.4);
          border-color: rgba(59, 130, 246, 0.8);
        }

        .error-button.secondary-button {
          background: rgba(255, 255, 255, 0.05);
          color: #f8fafc;
          border: 1px solid rgba(255, 255, 255, 0.15);
          backdrop-filter: blur(10px);
        }

        .error-button.secondary-button:hover {
          background: rgba(255, 255, 255, 0.1);
          border-color: rgba(255, 255, 255, 0.3);
          transform: translateY(-3px);
        }

        /* HINT TEXT */
        .error-hint {
          display: flex;
          align-items: center;
          gap: 8px;
          margin-top: 32px;
          font-size: 0.95rem;
          color: #64748b;
        }

        .error-link {
          color: #3b82f6;
          text-decoration: none;
          font-weight: 600;
          transition: all 0.2s ease;
          position: relative;
        }

        .error-link:hover {
          color: #60a5fa;
        }

        .error-link::after {
          content: '';
          position: absolute;
          bottom: -2px;
          left: 0;
          width: 0;
          height: 2px;
          background: #3b82f6;
          transition: width 0.3s ease;
        }

        .error-link:hover::after {
          width: 100%;
        }

        /* GRID BACKGROUND */
        .error-grid {
          position: absolute;
          inset: 0;
          background-image:
            linear-gradient(rgba(59, 130, 246, 0.03) 1px, transparent 1px),
            linear-gradient(90deg, rgba(59, 130, 246, 0.03) 1px, transparent 1px);
          background-size: 50px 50px;
          z-index: 1;
          pointer-events: none;
        }

        /* SCAN LINES EFFECT */
        .error-scan-lines {
          position: fixed;
          inset: 0;
          background-image: repeating-linear-gradient(
            0deg,
            rgba(0, 0, 0, 0.15),
            rgba(0, 0, 0, 0.15) 1px,
            transparent 1px,
            transparent 2px
          );
          background-size: 100% 2px;
          z-index: 20;
          pointer-events: none;
          animation: scan 8s linear infinite;
        }

        @keyframes scan {
          0% {
            transform: translateY(0);
          }
          100% {
            transform: translateY(10px);
          }
        }

        /* MOBILE RESPONSIVENESS */
        @media (max-width: 768px) {
          .error-content {
            padding: 20px;
            gap: 16px;
          }

          .error-actions {
            flex-direction: column;
            width: 100%;
          }

          .error-button {
            width: 100%;
            justify-content: center;
          }

          .error-hint {
            flex-direction: column;
            text-align: center;
          }

          .orb-1,
          .orb-2 {
            opacity: 0.5;
          }
        }
      `}</style>
    </div>
  )
}

export default page404
